    LinkTabs.__docgenInfo = { "description": "", "displayName": "LinkTabs", "props": { "tabs": { "defaultValue": null, "description": "List of tabs", "name": "tabs", "required": true, "type": { "name": "{ active?: boolean; link: string; title: string; }[]" } } } };
    // @ts-ignore
