import * as React from 'react';
import './collection-info.scss';

import * as moment from 'moment';
import { Link } from 'react-router-dom';

import {
  Banner,
  ClipboardCopy,
  Split,
  SplitItem,
  Grid,
  GridItem,
  FormSelect,
  FormSelectOption,
  Button,
} from '@patternfly/react-core';

import { DownloadIcon } from '@patternfly/react-icons';

import { CollectionDetailType, CollectionAPI } from 'src/api';
import { Tag } from 'src/components';
import { Paths, formatPath } from 'src/paths';
import { ParamHelper } from 'src/utilities/param-helper';
import { AppContext } from 'src/loaders/app-context';

interface IProps extends CollectionDetailType {
  params: {
    version?: string;
  };
  updateParams: (params) => void;
}

export class CollectionInfo extends React.Component<IProps> {
  downloadLinkRef: any;
  static contextType = AppContext;

  constructor(props) {
    super(props);
    this.downloadLinkRef = React.createRef();
  }

  render() {
    const {
      name,
      latest_version,
      namespace,
      all_versions,
      params,
      updateParams,
    } = this.props;

    let installCommand = `ansible-galaxy collection install ${namespace.name}.${name}`;

    if (params.version) {
      installCommand += `:${params.version}`;
    }

    const cloudWarning = document.location.hostname.match('cloud.redhat.com');

    return (
      <div className='pf-c-content info-panel'>
        <h1>Info</h1>
        <Grid hasGutter={true}>
          <GridItem>{latest_version.metadata.description}</GridItem>
          <GridItem>
            {latest_version.metadata.tags.map((tag, i) => (
              <Tag key={i}>{tag}</Tag>
            ))}
          </GridItem>

          <GridItem>
            <Split hasGutter={true}>
              <SplitItem className='install-title'>License</SplitItem>
              <SplitItem isFilled>{latest_version.metadata.license}</SplitItem>
            </Split>
          </GridItem>
          <GridItem>
            <Split hasGutter={true}>
              <SplitItem className='install-title'>Installation</SplitItem>
              <SplitItem isFilled>
                <ClipboardCopy isReadOnly>{installCommand}</ClipboardCopy>
                <div>
                  <b>Note:</b> Installing collections with ansible-galaxy is
                  only supported in ansible 2.9+
                </div>
                {cloudWarning ? (
                  <Banner variant='warning'>
                    Please log in to{' '}
                    <a href='https://console.redhat.com/'>console.redhat.com</a>{' '}
                    for downloads to work.
                  </Banner>
                ) : (
                  <div>
                    <a
                      ref={this.downloadLinkRef}
                      style={{ display: 'none' }}
                    ></a>
                    <Button
                      className='download-button'
                      variant='link'
                      icon={<DownloadIcon />}
                      onClick={() =>
                        this.download(
                          this.context.selectedRepo,
                          namespace,
                          name,
                          latest_version,
                        )
                      }
                    >
                      Download tarball
                    </Button>
                  </div>
                )}
              </SplitItem>
            </Split>
          </GridItem>
          <GridItem>
            <Split hasGutter={true}>
              <SplitItem className='install-tile'>Install Version</SplitItem>
              <SplitItem isFilled>
                <FormSelect
                  onChange={val =>
                    updateParams(ParamHelper.setParam(params, 'version', val))
                  }
                  value={
                    params.version ? params.version : latest_version.version
                  }
                  aria-label='Select collection version'
                >
                  {all_versions.map(v => (
                    <FormSelectOption
                      key={v.version}
                      value={v.version}
                      label={`${v.version} released ${moment(
                        v.created,
                      ).fromNow()} ${
                        v.version === latest_version.version ? '(latest)' : ''
                      }`}
                    />
                  ))}
                </FormSelect>
              </SplitItem>
            </Split>
          </GridItem>
          {latest_version.requires_ansible && (
            <GridItem>
              <Split hasGutter={true}>
                <SplitItem className='install-title'>
                  Requires Ansible
                </SplitItem>
                <SplitItem isFilled>
                  {latest_version.requires_ansible}
                </SplitItem>
              </Split>
            </GridItem>
          )}

          {latest_version.docs_blob.collection_readme ? (
            <GridItem>
              <div className='readme-container'>
                <div
                  className='pf-c-content'
                  dangerouslySetInnerHTML={{
                    __html: latest_version.docs_blob.collection_readme.html,
                  }}
                />
                <div className='fade-out'></div>
              </div>
              <Link
                to={formatPath(
                  Paths.collectionDocsIndexByRepo,
                  {
                    collection: name,
                    namespace: namespace.name,
                    repo: this.context.selectedRepo,
                  },
                  params,
                )}
              >
                Load full readme
              </Link>
            </GridItem>
          ) : null}
        </Grid>
      </div>
    );
  }

  private download(reponame, namespace, name, latest_version) {
    CollectionAPI.getDownloadURL(
      reponame,
      namespace.name,
      name,
      latest_version.version,
    ).then((downloadURL: string) => {
      // By getting a reference to a hidden <a> tag, setting the href and
      // programmatically clicking it, we can hold off on making the api
      // calls to get the download URL until it's actually needed. Clicking
      // the <a> tag also gets around all the problems using a popup with
      // window.open() causes.
      this.downloadLinkRef.current.href = downloadURL;
      this.downloadLinkRef.current.click();
    });
  }
}
try {
    // @ts-ignore
    CollectionInfo.displayName = "CollectionInfo";
    // @ts-ignore
    CollectionInfo.__docgenInfo = { "description": "", "displayName": "CollectionInfo", "props": { "params": { "defaultValue": null, "description": "", "name": "params", "required": true, "type": { "name": "{ version?: string; }" } }, "updateParams": { "defaultValue": null, "description": "", "name": "updateParams", "required": true, "type": { "name": "(params: any) => void" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/collection-detail/collection-info.tsx#CollectionInfo"] = { docgenInfo: CollectionInfo.__docgenInfo, name: "CollectionInfo", path: "src/components/collection-detail/collection-info.tsx#CollectionInfo" };
}
catch (__react_docgen_typescript_loader_error) { }